<template>
    <div class="moneyout">
        <!-- 头部开始 -->
        <header style="line-height: 0.5rem">
          <Head>
            <div slot='a' class="he shou_he">
              <div class="left">
                <router-link to="/Sou">
                  <a-Icon type="search" />
                </router-link>
              </div>
              <div class="content">
                确认订单
              </div>
              <div class="right">
                <router-link to="/Shou/My">
                  <a-Icon type="user" />
                </router-link>
              </div>
            </div>
          </Head>
        </header>
        <!-- 头部结束 -->
<div class="content">
        <!-- 地址开始 -->
        <div class="receive">
            <div>请添加一个收货地址</div>
            <van-icon name="arrow" class="van"/>
        </div>
        <!-- 地址结束 -->
        <!-- 送达时间开始 -->
        <div class="time">
            <h3>送达时间</h3>
            <div>
                <div class="t1">尽快送达 | 预计 17:12</div>
                <div class="t2">蜂鸟专送</div>
            </div>
        </div>
        <!-- 送达时间结束 -->
        <!-- 支付方式 -->
        <div class="way">
            <div style="border-bottom: 1px solid #f5f5f5">
              <div style="float: left;color: black">支付方式</div>
              <div style="float: right">在线支付
                <van-icon name="arrow" />
              </div>
            </div>
            <!-- /////////////////////// -->
            <div>
              <div style="float: left">红包</div>
              <div style="float: right">暂时只在饿了么 APP 中支持</div>
            </div>
        </div>

        <div class="name">效果演示</div>

        <!-- 商品开始 -->
        <div class="goods">
            <div>
              <div style="float: left">名字</div>
              <div style="float: right">
                  <span style="margin-right: 0.3rem;color:#EF5E27">x2</span>
                  <span>￥20</span>
              </div>
            </div>
           <!-- ///////////////////// -->
            <div>
              <div style="float: left">餐盒</div>
              <div style="float: right">￥801</div>
            </div>
            <!-- ///////////////////// -->
            <div>
              <div style="float: left">配送费</div>
              <div style="float: right">￥4</div>
            </div>
            <!-- ///////////////////// -->
            <div>
              <div style="float: left">订单￥845</div>
              <div style="float: right;color: #EF5E27">待支付￥845</div>
            </div>
            <!-- ///////////////////// -->

        </div>
        <!-- 商品结束 -->

        <!-- 订单备注 -->
        <div class="way" style="padding-bottom: 1rem">
          <div>
            <div style="float: left;color: black">订单备注</div>
            <div style="float: right">口味、偏好等
              <van-icon name="arrow" />
            </div>
          </div>
          <!-- /////////////////////// -->
          <div>
            <div style="float: left;color: black">发票抬头</div>
            <div style="float: right">不需要开发票
               <van-icon name="arrow" />
            </div>
          </div>
        </div>


</div>
        <!-- 尾部开始 -->
        <div class="foot">
            <div class="f1">待支付￥</div>
            <div class="f2">确 认 下 单</div>
        </div>
        <!-- 尾部结束 -->

    </div>
</template>

<script>
import Head from '@/page/header/Head'    
import axios from 'axios'    
export default{
    name:'Moneyout',
    components:{Head},
    mounted() {

        
        axios.post('https://elm.cangdu.org/v1/carts/checkout',
            {
                id:'',
                name:'',
                packing_fee:'',
                price:'',
                quantity:'',
                sku_id:'',
                stock:''
            }
        ).then((res)=>{

        })
    },
}
</script>

<style lang="scss"> 
.moneyout{
    width: 100%;
    height: 100%;
    background: #F5F5F5;
    position: relative;
    
    .content{
        width: 100%;
        display: flex;
        flex-direction: column;
        flex-grow: 1;
        padding-bottom: 0.6rem;
    }

    /* 商品开始 */
    .goods{
        width: 100%;
        height:2rem;
        background:white;
        margin-top:0.01rem;
        padding:0 5%;
        &>div{
            width: 100%;
            height:0.5rem;
            font-size: 0.18rem;
            line-height: 0.5rem;
        }
    }
    /* 商品结束 */

    .name{
        width: 100%;
        height:0.8rem;
        font-size:0.24rem;
        font-weight:600;
        line-height:0.8rem;
        padding-left:0.8rem;
        background:white;
        margin-top:0.1rem;
    }

    /* 支付方式 */
    .way{
        width: 100%;
        height:1rem;
        margin-top:0.1rem;
        background:white;
        color:gray;
        &>div{
            width:90%;
            height: 0.5rem;
            margin-left: 5%;
            line-height: 0.5rem;
        }
    }

    /* 送达时间开始 */
    .time{
        width: 100%;
        height:1rem;
        margin-top:0.1rem;
        background:white;
        border-left:0.05rem solid #518EE4;
        h3{
            width: 30%;
            text-align: center;
            line-height: 1rem;
            font-weight: 600;
            font-size: 0.2rem;
            float: left;
        }
        &>div{
            float: right;
            width: 70%;
            padding-right: 0.2rem;
            .t1{
                font-size: 0.16rem;
                color:#4685C5;
                text-align:right;
                line-height: 0.55rem;
            }
            .t2{
                width: 0.7rem;
                height: 0.2rem;
                background: #3F92EB;
                font-size: 0.12rem;
                color:white;
                text-align:center;
                line-height: 0.2rem;
                border-radius: 0.03rem;
                margin-left: 86%;
            }
        }
    }
    /* 送达时间结束 */

    /* 收货地址开始 */
    .receive{
        width: 100%;
        height:0.9rem;
        padding:0 0.2rem;
        line-height:0.8rem;
        font-size:0.2rem;
        border-bottom:2px dashed #99ACD5;
        background:white;
        div{
            float: left;
            color: black;
        }
        .van{
            float: right;
            line-height:0.8rem;
        }
    }
    /* 收货地址结束 */

    /* 尾部开始 */
    .foot{
        width: 100%;
        height:0.6rem;
        position:fixed;
        left:0;
        bottom:0;
        display:flex;
        color: white;
        font-size:0.2rem;
        line-height:0.6rem;
        .f1{
            width: 70%;
            background: #3D3D3D;
            padding-left: 0.3rem;
        }
        .f2{
            width: 30%;
            background: #69CF76;
            text-align: center;
        }
    }
    /* 尾部结束 */
}
</style>


